import helper from '../../common/common.js';
export default {
	data() {
		return {
			phonedata:'',
			datas:'',
			codedata:'',
			tips: '',
			// refCode: null,
			seconds: 60,
			timer:''
		}
	},
	methods: {
		codeChange(text) {
			this.tips = text;
		},
		getCode() {
			if (this.phonedata !== '') {
				  var reg=/^1[3456789]\d{9}$/;
				  if(!reg.test(this.phonedata)){
					this.callback('请输入有效的手机号码');
				  }else{
					  if(this.$refs.uCode.canGetCode) {
					  	// 模拟向后端请求验证码
					  	uni.showLoading({
					  		title: '正在获取验证码'
					  	})
					  	this.timer =setTimeout(() => {
					  		uni.hideLoading();
					  		// 这里此提示会被this.start()方法中的提示覆盖
					  		this.$u.toast('验证码已发送');
					  		// 通知验证码组件内部开始倒计时
					  		this.$refs.uCode.start();
					  	}, 2000);
					  }else {
					  	this.$u.toast('倒计时结束后再发送');
					  }
				  }
			}else{
				this.callback('手机号码不能为空');
			}
			console.log('执行请求')
			console.log(this.phonedata)
			uni.request({
				url:helper.websiteUrl+"base_sendalsms",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'phone':this.phonedata
				},
				success: (res) => {
					console.log(res.data);
					// 将请求到的数据存放放到data中
					this.datas = res.data.data;
					if(res.data.code == 304){
						this.callback('验证码过于频繁');
						uni.hideLoading();
						clearInterval(this.timer)
					}
				}
			});
		},
		back(){
			uni.navigateTo({
			    url: '../login/login'
			});
		},
		end() {
			this.$u.toast('倒计时结束');
		},
		start() {
			this.$u.toast('验证码已发送');
		},
		
		input(e){
			this.phonedata = e.detail.value
		},
		phoneinput(e){
			this.codedata = e.detail.value
		},
		callback(Error){
			 console.log("name",Error);
			this.$refs.uToast.show({
				title: Error,
				type: 'default',
				// url: '../tellogin/tellogin'
			})
			 
		},
		login(){
			if (this.phonedata !== '') {
				  var reg=/^1[3456789]\d{9}$/;
				  if(!reg.test(this.phonedata)){
					this.callback('请输入有效的手机号码');
				  }else{
					console.log(this.codedata)
					  if(this.datas == this.codedata){
					  	uni.request({
					  		url:helper.websiteUrl+"user_phonelogin",
					  		header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
					  		method:"post",
					  		data:{'phone':this.phonedata},
					  		success: (res) => {
					  			
					  			console.log(res.data);
					  			
					  			// 将请求到的数据存放放到data中
					  			uni.setStorage({
					  				key:'userinfo',
					  				data:res.data.data,
					  				success:()=>{}
					  			});
					  			this.$refs.uToast.show({
					  				title: '登录成功',
					  				type: 'default',
					  				url: '/pages/my/my',
					  				isTab:true
					  			})
					  		}
					  	});
					  }else if(this.codedata == ''){
						  this.callback('请输入验证码');
					  }else if(this.datas !==this.codedata ){
						  this.callback('验证码错误');
					}
				  }
			}else{
				this.callback('手机号码不能为空');
			}
			
			
				
		}
	}
}
